#chat {
  width: 80%;
  max-width: 800px;
  margin: auto;
}

#chat .history-msgs, #chat .unsend-msg {
  width: 100%;
  background-color: #fafafa;
  padding: 8px;
  box-sizing: border-box;
}

#chat .history-msgs {
  height: 500px;
  margin-bottom: 10px;
  overflow: auto;
}

#chat .history-msgs .msg-wrapper {
  margin-bottom: 10px;
}
#chat .history-msgs .msg-wrapper.me {
  text-align: right;
}

#chat .history-msgs .msg {
  display: inline-block;
  background: #eee;
  padding: 4px 12px;
  border-radius: 8px;
}

#chat .history-msgs .msg.me {
  background: rgb(163, 235, 91);
}

#chat .history-msgs .msg-wrapper.system {
  text-align: center;
  font-size: 12px;
  border-radius: 24px;
}

#chat .history-msgs .msg-wrapper.system a {
  text-decoration: none;
}

#chat .history-msgs .msg.system {
  border-radius: 24px;
}

#chat .unsend-msg {
  height: 100px;
  resize: none;
  outline: none;
  border: none;
}